import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
    //异步操作
    actions: {},
    //操作数据
    mutations: {
        // 保存x轴数据
        getXdata(state, val) {
            state.xData = val
        },
        // 保存发布任务数据
        startWork(state, val) {
            state.work = val
        },
        // 记录学生得分
        setScore(state, score) {
            switch (sessionStorage.getItem('subject')) {
                case 'html':
                    state.userInfo.html = score
                    break;
                case 'css':
                    state.userInfo.css = score
                    break;
                case 'js':
                    state.userInfo.js = score
                    break;
                default:
                    break;
            }
        },
        // 修改数值数据
        setting(state, res) {
            state.userInfo = res[0]
            res[0].src ? state.mySetting.avatar = res[0].src : state.mySetting.avatar = require('../assets/images/avatar/avatar1.png')
            res[0].fontfamily ? state.mySetting.fontFamily = res[0].fontfamily : state.mySetting.fontFamily = '微软雅黑'
            res[0].theme ? state.mySetting.themeColor = res[0].theme : state.mySetting.themeColor = '青色'
            res[0].color ? state.mySetting.fontColor = res[0].color : state.mySetting.fontColor = '黑色'
            res[0].background ? state.mySetting.loginBg = res[0].background : state.mySetting.loginBg = require('../assets/images/login/loginbg1.png')
                // 改字体样式
            switch (state.mySetting.fontFamily) {
                case '微软雅黑':
                    document.querySelector('html').style.fontFamily = 'Microsoft YaHe'
                    break;
                case '宋体':
                    document.querySelector('html').style.fontFamily = 'SimSun'
                    break;
                case '仿宋':
                    document.querySelector('html').style.fontFamily = 'FangSong'
                    break;
                case '楷体':
                    document.querySelector('html').style.fontFamily = 'KaiTi'
                    break;
                case '幼圆':
                    document.querySelector('html').style.fontFamily = 'YouYuan'
                    break;
                default:
                    document.querySelector('html').style.fontFamily = 'Microsoft YaHe'
                    break;
            }
            // 改主题以及颜色样式
            let arr = document.querySelector('html').className.split(' ')
                // 修改主题类
            switch (state.mySetting.themeColor) {
                case '青色':
                    arr.splice(0, 1, 'theme1')
                    break;
                case '粉色':
                    arr.splice(0, 1, 'theme2')
                    break;
                case '紫色':
                    arr.splice(0, 1, 'theme3')
                    break;
                case '蓝色':
                    arr.splice(0, 1, 'theme4')
                    break;
                case '橙色':
                    arr.splice(0, 1, 'theme5')
                    break;
                case '绿色':
                    arr.splice(0, 1, 'theme6')
                    break;
                case '红色':
                    arr.splice(0, 1, 'theme7')
                    break;
                default:
                    arr.splice(0, 1, 'theme1')
                    break;
            }
            // 修改颜色类
            switch (state.mySetting.fontColor) {
                case '黑色':
                    arr.splice(1, 1, 'color1')
                    break;
                case '粉红色':
                    arr.splice(1, 1, 'color2')
                    break;
                case '淡紫色':
                    arr.splice(1, 1, 'color3')
                    break;
                case '蓝色':
                    arr.splice(1, 1, 'color4')
                    break;
                case '绿色':
                    arr.splice(1, 1, 'color5')
                    break;
                default:
                    arr.splice(1, 1, 'color1')
                    break;
            }
            let str = arr.join().replace(',', ' ')
            document.querySelector('html').className = str
        },
    },
    // 存放全局共享数据
    state: {
        // x轴数据
        xData: '',
        // 设置
        mySetting: {
            // 头像
            avatar: require('../assets/images/avatar/avatar1.png'),
            // 背景图片
            loginBg: require('../assets/images/login/loginbg1.png'),
            // 字体样式
            fontFamily: '微软雅黑',
            // 主题颜色
            themeColor: '青色',
            // 字体颜色
            fontColor: '黑色',
            // 用户id
            id: sessionStorage.getItem('id')
        },
        // 用户信息
        userInfo: {},
        // 是否已发布任务，接收发布任务后传过来的数据
        work: ''
    },
    //计算属性
    getters: {},
    //模块化的Vuex
    modules: {}
})