<template>
  <div class="test">
    <el-card shadow="never">
    <el-button type="primary" icon="el-icon-arrow-left" circle @click="toAnswer"></el-button>
    <div class="testing" v-for="item in nowTestInfo" :key="item.id">
        <p>第<span>{{index+1}}</span>题：{{item.title}}</p>
          <el-radio-group v-model="option">
    <el-radio label="A">A {{item.optionA}}</el-radio>
    <el-radio label="B">B {{item.optionB}}</el-radio>
    <el-radio label="C">C {{item.optionC}}</el-radio>
    <el-radio label="D">D {{item.optionD}}</el-radio>
  </el-radio-group> <br>
  <el-button @click="next">下一题</el-button>
  <el-button @click="submit">提交</el-button>
    </div>
    </el-card>

  </div>
</template>

<script>
export default {
  data(){
    return{
      // 单选框选中项
      option:'',
      // 全部测试题信息
      allTestInfo:[],
      // 当前测试题
      nowTestInfo:[],
      // 当前题号
      index:0,
      // 用于保存已完成的选择题的选项
      options:[],
      // 用于保存选择题的正确答案
      answer:[],
      // 得分
      score:'',
    }
  },
 methods:{
  // 回退到开始答题页面
  toAnswer(){
    this.$router.go(-1)
  },
  // 获取当前测试题的信息
  async getTest(){
     let {data:res} = await this.$axios({
       url:`test/getTest?type=${sessionStorage.getItem('subject')}&number=${sessionStorage.getItem('number')}`
      })
      this.allTestInfo = res.data
      // 根据索引，拿到当前题目
     this.nowTestInfo = this.allTestInfo.filter((item,index) => {
          return index == this.index
      })
  },
  // 点击下一题
  next(){
    if( this.option==''){
     this.$message.warning('请先选择1个答案')
    }else{
    // 将已经完成的题目的选项，保存起来
    if(this.options.length<5){
    this.options.push(this.option)
    }
    if(this.index===4){
    this.$message.warning('已经是最后一道题了')
    }else{
   this.index++
   this.option=''
    // 根据索引，拿到当前题目
     this.nowTestInfo = this.allTestInfo.filter((item,index) => {
          return index == this.index
      })
    }
    }
  },
  // 点击提交任务
  async submit(){
    // 如果不是最后1道题
  if(this.options.length<4){
    this.$message.warning('请先填写完题目后，再进行提交')
  }else{
    // 当前是最后1题
    // 如果没选择选项
     if(this.option==''){
  this.$message.warning('请先填写完题目后，再进行提交')
 }else{
    //  如果数组没有保存当前选项
     if(this.options.length<5){
     this.options.push(this.option)
       }
      //  遍历正确答案数组，将正确答案选项取出来
    this.allTestInfo.forEach((item) => {
      this.answer.push(item.answer)
  })
    // 计算分数
  for (let i = 0; i < this.allTestInfo.length; i++) {
   if(this.options[i]==this.answer[i]){
    this.score=20+(+this.score)
   }
  }
  // tu如果1个都不对，把0赋值给score
  this.score=='' ? this.score=0 : this.score==this.score
  // 发送ajax，将分数保存
       this.$axios({
       url:`users/score`,
       method:'put',
       data:{
        score:this.score,
        type:sessionStorage.getItem('subject'),
        id:this.$store.state.userInfo.id
       }
      })
      this.$store.commit('setScore',this.score)
     this.$router.replace('/administration/answer')
   
 }
  }
  }
 },
 created(){
  this.getTest()
 },
}
</script>

<style lang="less" scoped>
.test{
  text-align: left;
   height: 100%;
.el-card{
  height: 100%;
}
.testing{
  text-align: center;
  font-size: 30px;
  letter-spacing:2px;
}
.el-radio{
  scale: (1.5);
  margin:50px 70px;
}
}

</style>