<template>
<div class="release">
    <el-card shadow="never">
    <div class="top">
    <el-dropdown split-button type="primary" @command="handleCommand">
     html
  <el-dropdown-menu slot="dropdown">
    <el-dropdown-item command="html 1">第一套</el-dropdown-item>
    <el-dropdown-item command="html 2">第二套</el-dropdown-item>
    <el-dropdown-item command="html 3">第三套</el-dropdown-item>
  </el-dropdown-menu>
</el-dropdown>
    <el-dropdown split-button type="primary" @command="handleCommand">
     css
  <el-dropdown-menu slot="dropdown">
    <el-dropdown-item command="css 1">第一套</el-dropdown-item>
    <el-dropdown-item command="css 2">第二套</el-dropdown-item>
    <el-dropdown-item command="css 3">第三套</el-dropdown-item>
  </el-dropdown-menu>
</el-dropdown>
    <el-dropdown split-button type="primary" @command="handleCommand">
     js
  <el-dropdown-menu slot="dropdown">
    <el-dropdown-item command="js 1">第一套</el-dropdown-item>
    <el-dropdown-item command="js 2">第二套</el-dropdown-item>
    <el-dropdown-item command="js 3">第三套</el-dropdown-item>
  </el-dropdown-menu>
</el-dropdown>

<el-button type="success" :disabled="$store.state.userInfo.job=='学生'" @click="addHistory">发布任务</el-button>
    </div>

  <div>
      <div class="list" v-if="historyData.length">
        <ul>
            <li v-for="item in historyData" :key="item.id">
                <p>
                  <a v-if="item.date==myHistory.date">任 务 进 行 中......。</a>
                  <a v-else>任务已结束</a>
              任 务 详 情：（html第<span>{{item.html}}</span>套，css第<span>{{item.css}}</span>套，js第<span>{{item.js}}</span>套）
                   由 <span>{{item.name}}</span>老 师 发 布，<span>{{item.date}}</span></p>
            </li>
        </ul>
    </div>
    <el-empty description="还没有发表过任务哦" v-else></el-empty>
  </div>



    </el-card>
</div> 
</template>

<script>
export default {
  data(){
    return{
      // 添加历史记录数据
      myHistory:{
        html:1,
        css:1,
        js:1,
        name:'',
        class:'',
        date:''
      },
      // 获取的历史记录信息
      historyData:[]
    }
  },
        methods: {
      // 获取数据库保存的历史记录
       async getHistory(){
          this.myHistory.class = this.$store.state.userInfo.class
          let {data:res} = await this.$axios({
            url:`history/getHistory?class=${this.myHistory.class}`
          })
          this.historyData = res.data        
        },
          // 点击下拉菜单选项触发
     handleCommand(command) {
      let arr = command.split(' ')
      switch (arr[0]) {
        case 'html':
          this.myHistory.html = arr[1]
          break;
            case 'css':
          this.myHistory.css = arr[1]
          break;
            case 'js':
          this.myHistory.js = arr[1]
          break;
        default:
          break;
      }
    },
    // 追加历史记录
   async addHistory(){
       this.myHistory.name = this.$store.state.userInfo.name
       this.myHistory.class = this.$store.state.userInfo.class
          if(this.historyData.length==0){
           this.historyData = [{date:'1900/1/1'}]
          } 
       if (this.historyData[0].date==this.myHistory.date) {
        // 在同一天，任务已经发布过来，无法再发布
       this.$message.warning('今天已经发布过任务了，无法重复发布')
       }else{
        // 不在同一天，发布任务
          let {data:res} = await this.$axios({
            url:`history/addHistory`,
            method:'post',
            data:this.myHistory
          })
          if(res.status==200){
            this.$message.success('发布成功')
            this.getHistory()
          }else{this.$message.success('发布失败')}
       }
     
    }
  
    },
    created(){
        let date = new Date()
       this.myHistory.date = date.toLocaleDateString()
      setTimeout(() => {
         this.getHistory()
      }, 0);
    }

}
</script>

<style lang="less" scoped>
.release{
  text-align: left;
}
.el-dropdown + .el-dropdown,.el-button {
    margin: 40px 0;
    margin-left: 20px;
}
.top{
    border-bottom: 1px solid gray;
}
.list{
li{
    list-style: none;
    width: 80%;
    height: 30px;
    background-color: var(--theme);
    border-radius: 15px;
    line-height: 30px;
    padding: 0 15px;
    box-sizing: border-box;
    color: white;
span{
   color: red;
}
}
}
</style>