<template>
<div class="administration">
    <el-container>
     <!-- 头部区域 -->
     <el-header> 
      <img src="../assets/images/administration/logo.png" alt="">
      <h2>管 理 后 台</h2>
      <div class="headerRight">
          <!-- 头像 -->
       <el-avatar :src="avatar"></el-avatar>
       <!-- 退出登录 -->
       <i class="el-icon-switch-button edit" @click="editLogin">&nbsp; 退出登录</i>
      </div>
     </el-header>
     <el-container>
         <!-- 侧边栏区域 -->
     <el-aside width="200px">
     <el-menu
      :default-active="activeIndex"
      class="el-menu-vertical-demo"
      background-color="var(--theme)"
      text-color="#fff"
      active-text-color="#009900"
      :unique-opened="true"
      :router="true">
        <el-menu-item index="welcome">
        <template slot="title">
          <i class="el-icon-s-home"></i>
          <span>欢迎</span>
        </template>
      </el-menu-item>
        <el-submenu index="user">
        <template slot="title">
          <i class="el-icon-user"></i>
          <span>师生信息</span>
        </template>
          <el-menu-item index="usersInfo"> <i class="el-icon-s-operation"></i>师生名单</el-menu-item>
      </el-submenu>
       <el-submenu index="test">
        <template slot="title">
          <i class="el-icon-tickets"></i>
          <span>任务板块</span>
        </template>
          <el-menu-item index="release"><i class="el-icon-coin"></i>发布任务</el-menu-item>    
          <el-menu-item index="answer"><i class="el-icon-edit"></i>开始任务</el-menu-item>
          <el-menu-item index="score"><i class="el-icon-s-operation"></i>成绩单</el-menu-item>
      </el-submenu>
       <el-menu-item index="setting">
        <template slot="title">
          <i class="el-icon-location"></i>
          <span>设置</span>
        </template>
      </el-menu-item>
     </el-menu>

     </el-aside>
      <!-- 主体区域 -->
     <el-main>
        <router-view></router-view>
     </el-main>
     </el-container>
  </el-container>



</div>
</template>

<script>
export default {
 data(){
    return{
        // 当前激活的菜单项
        activeIndex:'welcome',
        // 头像
        avatar:require('../assets/images/avatar/avatar1.png'),
        // 存储用户信息
        userInfo:''
    }
 },
 methods:{
  // 退出登录
 editLogin(){
    sessionStorage.removeItem('token')
    document.querySelector('html').className ='theme1 color1'
    this.$router.replace('/login')
 },
   // 获取用户信息
  async getUserInfo(){
     let { data:res} = await this.$axios({
      url:`users/getuserInfo?id=${sessionStorage.getItem('id')}`
    })
    this.userInfo = res.data
   if(!!res.data[0].src){
     this.avatar = res.data[0].src
   }
   },
  //  重置学生分数
  resetScore(){
     let date = new Date()
    //  今天日期
    date = date.toLocaleDateString()
    if(date==localStorage.getItem('date')){
      // 在同一天 不重置分数
      return true
    }else{
      // 不在同一天，重置分数，开始新的任务，重新开始记分
         this.$axios({
          url:`users/resetScore`
          })
          // 将本地存储日期同步到今天
        localStorage.setItem('date',date)
    }
  }
 },
created(){
  // 绑定自定义事件获取头像
  this.$bus.$on('avatar',(val) => {
    this.avatar = val
  })
  // 拉取服务器设置数据
  this.getUserInfo().then(() => {
      this.$store.commit('setting',this.userInfo)
  })
  this.resetScore()
},
}
</script>

<style lang="less" scoped>
.administration{
    height: 100%;
.el-container{
    height: 100%;
}
.el-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100px !important;
    background-color: var(--theme);
    color: var(--color);
    text-align: center;
    line-height: 100px;
img{
        width: 100px;
}
.headerRight{
    display: flex;
    justify-content: space-between;
    align-items: center;
.el-avatar{
    margin: 0 15px;
    width: 80px;
    height: 80px;
}
}
h2{
  font-size: 30px;
}
}
  
.el-aside {
.el-menu-item {
    min-width: 0;
}
}
  
.el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    padding:10px ;
}
.edit{
  cursor: pointer;
  transition: all .5s;
  &:hover{
    transform: scale(1.05);
  }
}
}
</style>