<template>
  <div class="setting">
     <el-card shadow="never">
       <div class="top">
          <!-- 头像 -->
       <div class="headPortrait">
         <el-avatar :src="mySetting.avatar"></el-avatar>
       <el-button plain @click="openDialogAvatar">更改头像</el-button>
       </div>
       <!-- 用户信息 -->
       <div class="userinfo">
        <ul>
          <li>
            姓名：
            <span>{{userInfo.name}}</span>
            性别：
             <span>{{userInfo.sex}}</span>
          </li>
           <li>
            年龄：
            <span>{{userInfo.age}}</span>
            班级：
             <span>{{userInfo.class}}</span>
          </li>
            <li>
            身份：
            <span>{{userInfo.job}}</span>
            联系方式：
             <span>{{userInfo.phone}}</span>
          </li>
        </ul>
       </div>
       </div>
     <div class="bottom">
      <!-- 表单 -->
        <el-form ref="settingForm" label-width="50px">

     <el-form-item label="字体样式" label-width="80px" style="text-align:left">
        <el-radio-group style="margin-left:25px" v-model="mySetting.fontFamily">
        <el-radio label="微软雅黑"></el-radio>
        <el-radio label="宋体"></el-radio>
        <el-radio label="仿宋"></el-radio>
         <el-radio label="楷体"></el-radio>
          <el-radio label="幼圆"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="主题颜色" label-width="80px" style="text-align:left">
        <el-radio-group style="margin-left:25px" v-model="mySetting.themeColor">
        <el-radio label="青色"></el-radio>
        <el-radio label="粉色"></el-radio>
         <el-radio label="紫色"></el-radio>
          <el-radio label="蓝色"></el-radio>
           <el-radio label="橙色"></el-radio>
            <el-radio label="绿色"></el-radio>
             <el-radio label="红色"></el-radio>
        </el-radio-group>
      </el-form-item>
        <el-form-item label="字体颜色" label-width="80px" style="text-align:left">
        <el-radio-group style="margin-left:25px" v-model="mySetting.fontColor">
        <el-radio label="黑色"></el-radio>
        <el-radio label="粉红色"></el-radio>
        <el-radio label="淡紫色"></el-radio>
        <el-radio label="蓝色"></el-radio>
        <el-radio label="绿色"></el-radio>
        </el-radio-group>
      </el-form-item>
        <el-form-item label="登录背景" label-width="80px" style="text-align:left">
         <el-button plain @click="openDialogLoginBg">更改登录背景</el-button>
      </el-form-item>
      <el-button type="primary" plain>取消</el-button>
      <el-button type="primary" plain @click="keep">保存</el-button>
       </el-form>
     </div>      
    </el-card>

    <!-- 头像模态框 -->
<el-dialog
  title="提示"
  :visible.sync="dialogAvatar"
  width="50%"
   >
  <div class="pictures" ref="pictures">
   <img src="../assets/images/avatar/avatar1.png" alt="" @click="getSrc">
   <img src="../assets/images/avatar/avatar2.png" alt="" @click="getSrc">
   <img src="../assets/images/avatar/avatar3.png" alt="" @click="getSrc">
   <img src="../assets/images/avatar/avatar4.png" alt="" @click="getSrc">
   <img src="../assets/images/avatar/avatar5.png" alt="" @click="getSrc">
   <img src="../assets/images/avatar/avatar6.png" alt="" @click="getSrc">
   <img src="../assets/images/avatar/avatar7.png" alt="" @click="getSrc">
   <img src="../assets/images/avatar/avatar8.png" alt="" @click="getSrc">
   <img src="../assets/images/avatar/avatar9.png" alt="" @click="getSrc">
   <img src="../assets/images/avatar/avatar10.png" alt="" @click="getSrc">
   <img src="../assets/images/avatar/avatar11.png" alt="" @click="getSrc">
   <img src="../assets/images/avatar/avatar12.png" alt="" @click="getSrc">
  </div>
</el-dialog>

    <!-- 登录背景模态框 -->
<el-dialog
  title="选择1个背景"
  :visible.sync="dialogLoginBg"
  width="50%"
   >
  <div class="pictures2" ref="pictures2">
   <img src="../assets/images/login/loginbg1.png" alt="" @click="getBgSrc">
   <img src="../assets/images/login/loginbg2.png" alt="" @click="getBgSrc">
   <img src="../assets/images/login/loginbg3.png" alt="" @click="getBgSrc">
   <img src="../assets/images/login/loginbg4.png" alt="" @click="getBgSrc">
   <img src="../assets/images/login/loginbg5.png" alt="" @click="getBgSrc">
  </div>
</el-dialog>


  </div>
</template>

<script>
export default {
  data(){
    return{
      // 控制单选按钮
       resource: '',
      //  个人信息
      userInfo:{},
       userInfo2:{},
            // 是否保存
        ifKeep:false,
           // 图片模态框
        dialogAvatar:false,
        // 登录背景图片模态框
        dialogLoginBg:false,
        // 设置
      mySetting:{
        // 头像
        avatar:require('../assets/images/avatar/avatar1.png'),
        // 背景图片
        loginBg:require('../assets/images/login/loginbg1.png'),
        // 字体样式
        fontFamily:'微软雅黑',
        // 主题颜色
        themeColor:'青色',
        // 字体颜色
        fontColor:'黑色',
        // 用户id
        id:sessionStorage.getItem('id')
      }
    }
  },
  methods:{
    // 获取用户信息
  async getUserInfo(){
     let { data:res} = await this.$axios({
      url:`users/getuserInfo?id=${sessionStorage.getItem('id')}`
    })
   this.userInfo = res.data[0]
   this.userInfo2 = res.data
     if(!!res.data[0].src) this.mySetting.avatar = res.data[0].src
     if(!!res.data[0].fontfamily) this.mySetting.fontFamily = res.data[0].fontfamily
     if(!!res.data[0].theme) this.mySetting.themeColor = res.data[0].theme
     if(!!res.data[0].color) this.mySetting.fontColor = res.data[0].color
     if(!!res.data[0].background) this.mySetting.loginBg = res.data[0].background
   },
  //  获取图片路径
   getSrc(e){
    // let reg = /avatar([0-9])/
    // let result = reg.exec(e.target.src)
    //  this.mySetting.avatar = `../assets/images/avatar/avatar${result[1]}.png`
       this.mySetting.avatar = e.target.src
     this.dialogAvatar = false
   },
   //  获取背景图片路径
   getBgSrc(e){
     this.mySetting.loginBg = e.target.src
     this.dialogLoginBg = false
   },
  //  更改主题配置
  setting(){
   // 改字体样式
switch (this.mySetting.fontFamily) {
    case '微软雅黑':
    document.querySelector('html').style.fontFamily = 'Microsoft YaHe'
    break;
    case '宋体':
    document.querySelector('html').style.fontFamily = 'SimSun'
    break;
    case '仿宋':
    document.querySelector('html').style.fontFamily = 'FangSong'
    break;
    case '楷体':
    document.querySelector('html').style.fontFamily = 'KaiTi'
    break;
    case '幼圆':
    document.querySelector('html').style.fontFamily = 'YouYuan'
    break;
  default:
    break;
}
// 改主题以及颜色样式
let arr =  document.querySelector('html').className.split(' ')
// 修改主题类
switch (this.mySetting.themeColor) {
    case '青色':
    arr.splice(0,1,'theme1')
    break;
    case '粉色':
    arr.splice(0,1,'theme2')
    break;
    case '紫色':
    arr.splice(0,1,'theme3')
    break;
    case '蓝色':
    arr.splice(0,1,'theme4')
    break;
    case '橙色':
    arr.splice(0,1,'theme5')
    break;
     case '绿色':
    arr.splice(0,1,'theme6')
    break;
     case '红色':
    arr.splice(0,1,'theme7')
    break;
  default:
    break;
}
// 修改颜色类
switch (this.mySetting.fontColor) {
    case '黑色':
    arr.splice(1,1,'color1')
    break;
    case '粉红色':
    arr.splice(1,1,'color2')
    break;
    case '淡紫色':
    arr.splice(1,1,'color3')
    break;
    case '蓝色':
    arr.splice(1,1,'color4')
    break;
    case '绿色':
    arr.splice(1,1,'color5')
    break;
  default:
    break;
}
let str =  arr.join().replace(',',' ')
document.querySelector('html').className =str
  },
  // 打开更改头像模态框
  openDialogAvatar(){
  this.dialogAvatar = true
  this.$nextTick(() => {
   [...this.$refs.pictures.children].forEach((item) => {
    if(item.src == this.mySetting.avatar){
      item.style.outline = '8px red solid'
    }else{
      item.style.outline = ''
    }
   })
  })
  },
  // 打开登录背景模态框
  openDialogLoginBg(){
 this.dialogLoginBg = true
  this.$nextTick(() => {
   [...this.$refs.pictures2.children].forEach((item) => {
    console.log(item.src);
    if(item.src == this.mySetting.loginBg){
      item.style.outline = '8px red solid'
    }else{
      item.style.outline = ''
    }
   })
  })
  },
  //  保存修改
  async keep(){
    this.ifKeep = true
     let { data:res} = await this.$axios({
      method:'put',
      url:`users/setting`,
      data:this.mySetting
    })
    if(res.status==200){
       this.$message.success('保存成功')
     this.$bus.$emit('avatar',this.mySetting.avatar)
    }else{
       this.$message.error('修改失败')
    }
    
  },
  // 打开是否更改提示框
  //  open(next) {
  //       this.$confirm('您还没有保存更改，请点击先保存', '提示', {
  //         confirmButtonText: '去保存',
  //         cancelButtonText: '不保存',
  //         type: 'warning'
  //       }).then(() => {

  //       }).catch(() => {
  //           this.getUserInfo()
  //         next()
  //         this.$message({
  //           type: 'info',
  //           message: '已取消更改'
  //         });          
  //       });
  //     }
    
  },
  watch:{
   mySetting:{
    handler(){
      this.setting()
    },
    deep:true
   }
  },

  created(){
   this.getUserInfo()

  },
  // 在离开当前页面前提示
  beforeRouteLeave(to,from,next){
    this.getUserInfo().then(() => {
         this.$store.commit('setting',this.userInfo2)
    })
    next()
  },  
}
</script>

<style lang="less" scoped>
.setting{
.el-card{
.top{
  display: flex;
  justify-content: space-evenly;
.headPortrait{
  display: flex;
  flex-direction: column;
  align-items: center;
.el-avatar{
    margin: 15px 0;
    width: 80px;
    height: 80px;
}
}
.userinfo{
li{
  list-style: none;
  line-height: 45px;
  height: 45px;
  text-align: left;
  opacity: .8;
  user-select: none;
  color: var(--color);
}
span{
   margin-right: 25px;
}
}
}
.bottom{
  width: 60%;
  margin: 0 auto;
.el-form{
  line-height: 30px;
}
}
}
.pictures{
  img{
    width: 100px;
    margin: 10px;
    transition: all .5s;
    cursor: pointer;
    &:hover{
      scale: (1.1);
    }
  }
}
.pictures2{
  img{
    width: 200px;
    margin: 15px;
    transition: all .5s;
    cursor: pointer;
    &:hover{
      scale: (1.1);
    }
  }
}

}
</style>