<template>
  <div class="welcome">
      <el-card shadow="hover" class="card1">
     <h3>网站访问总量</h3>
      <span>{{visited}}</span>
    </el-card>
      <el-card shadow="hover" class="card2">
      <h3>现在是</h3>
      <span>{{time}}</span>
    </el-card>
      <el-card shadow="hover" class="card3">
       <h3>已注册</h3>
       <div id="myCharts2"></div>
    </el-card>
    <!-- 折线图 -->
     <el-card shadow="hover" class="card4">
      <div id="myCharts"></div>
    </el-card>
  </div>
</template>

<script>
export default {
  data(){
    return{
       // 网站访问量
        visited:0,
        // 在线人数
        time:new Date().toLocaleString(),
        // 获取已注册人数
        registered:'1',
        // 获取未注册人数
        unRegister:'1',
        // 获取x轴访问人数
        xData:'',
        // x轴坐标项
        xAxis:''
    }
  },
  methods:{
    // 获取饼状图数据
 async getUsers(){
    let { data:res} = await this.$axios({
      url:'users/getusers'
    })
   let register = res.data.filter((item) => {
      return item.register == '已注册'
    })
       let unRegister = res.data.filter((item) => {
      return item.register == '未注册'
    })
    this.registered = register.length
    this.unRegister = unRegister.length
  },
// 获取x轴坐标项
   async getXAxis(){
        let {data:res} = await this.$axios({
      url:'users/readXaxis'
    })
    this.xAxis = res.data
    },
    // 获取访问量
       async getVisited(){
        let {data:res} = await this.$axios({
      url:'users/getVisited'
    })
          this.visited =  res.data
    },

  },
    mounted(){    
 const  myCharts = echarts.init(document.querySelector('#myCharts'));
  const  myCharts2 = echarts.init(document.querySelector('#myCharts2'));

setTimeout(() => {
     const options ={
  title: {
    text: '网站访问7日历史记录（仅访问会产生记录）'
  },
      // 鼠标hover提示
    tooltip: {
    trigger: 'item'
  },
 xAxis: {
    type: 'category',
    data: this.xAxis
  },
  yAxis: {
    type: 'value',
  },
  series: [
    {
      data: this.xData,
      type: 'line'
    }
  ]
}
 myCharts.setOption(options);
    const options2 ={
    // 鼠标hover提示
    tooltip: {
    trigger: 'item'
  },
    legend: {
    orient: 'vertical',
    left: 'left',
    bottom:0
  },
    color: ['#99FF00', '#FF6633'],
  series: [
    {
      type: 'pie',
      data: [
        {
          value: this.registered,
          name: '已注册'
      },
        {
          value: this.unRegister,
          name: '未注册'
      }
      ]
    }]
 }
  myCharts2.setOption(options2);
}, 500);

    },
created(){
  setInterval(() => {
      let date = new Date()
    this.time = date.toLocaleString()
  }, 1000);
 this.getUsers()
 this.getXAxis()
 this.getVisited()
if(this.$store.state.xData){
   localStorage.setItem('xData',this.$store.state.xData,1)
}
this.xData = localStorage.getItem('xData').split(',')
},



}
</script>

<style lang="less" scoped>
.welcome{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
.card1,.card2{
   background-repeat: no-repeat;
  background-position: left bottom;
  background-color: var(--theme);
  background-size: 50%;
  background-image: url('../assets/images/dialog/dialog7.png');
}
.card3,.card4{
    background-color: var(--theme);
}
.card2{
    background-size: 30%;
 background-image: url('../assets/images/dialog/dialog6.png');
}
.el-card{
    width: 400px;
    margin: 15px 0;
      color: #333;
h3{
  font-size: 30px;
  margin: 10px 0 0 0;
}
span{
  font-size: 50px;
  color: #eee;
  font-weight: 700;
  display: block;
  margin-top: 40px;
}
#myCharts{
  width: 100%;
  height: 300px;
}
#myCharts2{
  width: 100%;
  height: 200px;
}
}
& .el-card:last-of-type{
    width: 100%;
    height: 300px;
}
}

</style>