<template>
  <div class="Login bg1">
     
     <!-- 去登录 -->
     <div class="gologin">
         <h2>师 生 后 台 管 理 系 统</h2>
         <el-button plain round @click="openDialogLogin">登录</el-button>
         <el-button  plain round @click="dialogRegister = true">注册</el-button>
     </div>

     <!-- 登录框 -->
     <div class="login">
        <el-dialog
         title="登录"
         width="30%"
         :visible.sync="dialogLogin"
        >
       <el-form ref="loginForm" label-width="80px" :rules="registerRules" :model="loginData">
        <el-form-item label="用户名" prop="userName">
        <el-input v-model="loginData.userName"></el-input>
       </el-form-item>
         <el-form-item label="密码" prop="password2">
        <el-input v-model="loginData.password" type="password"></el-input>
       </el-form-item>
         <el-form-item label="身份码" prop="id">
        <el-input v-model.number="loginData.id"></el-input>
       </el-form-item>
         <el-form-item class="aboutPwd">
      <el-checkbox label="记住密码" name="type" v-model="rememberPwd" @change="removeItem"></el-checkbox>
         </el-form-item>
       </el-form>
       <span slot="footer" class="dialog-footer">
       <el-button @click="dialogLogin = false">取 消</el-button>
       <el-button type="primary" @click="toAdministration">确 定</el-button>
      </span>
     </el-dialog>
     </div>

     <!-- 注册框 -->
     <div class="register">
        <el-dialog
         title="注册"
         width="30%"
         :visible.sync="dialogRegister"
        >
       <el-form ref="registerForm" label-width="80px" :rules="registerRules" :model="registerData">
        <el-form-item label="用户名" prop="userName">
        <el-input v-model="registerData.userName"></el-input>
       </el-form-item>
         <el-form-item label="密码" prop="password">
        <el-input v-model="registerData.password" type="password"></el-input>
       </el-form-item>
       <el-form-item label="确认密码" prop="repeatPwd">
        <el-input v-model="registerData.repeatPwd"  type="password"></el-input>
       </el-form-item>
        <el-form-item label="编号" prop="id">
        <el-input placeholder="请提供您的编号号码" v-model.number="registerData.id"></el-input>
       </el-form-item>
       <el-form-item label="姓名" prop="name">
        <el-input placeholder="请填写您的真实姓名" v-model="registerData.name"></el-input>
       </el-form-item>
       </el-form>
       <span slot="footer" class="dialog-footer">
       <el-button @click="closeDialogRegister">取 消</el-button>
       <el-button @click="registerUser">确 定</el-button>
      </span>
     </el-dialog>
     </div>

       <!-- 找回密码框 -->
     <div class="retrievepwd">
        <el-dialog
         title="找回密码"
         width="30%"
         :visible.sync="dialogRetrievepwd"
        >
       <el-form ref="form" label-width="80px">
        <el-form-item label="账号">
        <el-input></el-input>
        </el-form-item>
       </el-form>
       <span slot="footer" class="dialog-footer">
       <el-button @click="dialogRetrievepwd = false">取 消</el-button>
       <el-button type="primary" @click="dialogRetrievepwd = false">确 定</el-button>
      </span>
     </el-dialog>
     </div>

    <!-- 注册成功模态框 -->
        <el-dialog
         title=""
         width="30%"
         :visible.sync="dialogRegisterSuccess"
        >
     <el-result icon="success" title="注册成功"
      :subTitle="'用户名:'+ userInfo.userName + ` 密码:`+userInfo.password">
      <template slot="extra">
        <el-button size="medium" @click="openLoginDialog">去登录</el-button>
      </template>
    </el-result>
      </el-dialog>

          <!-- 注册失败模态框 -->
        <el-dialog
         title=""
         width="30%"
         :visible.sync="dialogRegisterFail"
        >
     <el-result icon="error" title="注册失败，不存在这个用户" subTitle="请重新尝试">
      <template slot="extra">
         <el-button type="primary" size="medium" @click="openRegisterDialog">返回注册</el-button>
      </template>
    </el-result>
      </el-dialog>
     
              <!-- 用户名已被专用模态框 -->
        <el-dialog
         title=""
         width="30%"
         :visible.sync="dialogOccupy"
        >
     <el-result icon="info" title="注册失败" subTitle="用户名已被占用，请重新注册">
      <template slot="extra">
         <el-button type="primary" size="medium" @click="openRegisterDialog">返回注册</el-button>
      </template>
    </el-result>
      </el-dialog>

      <!-- 用户已经注册过模态框 -->
  <el-dialog
         title=""
         width="30%"
         :visible.sync="alreadyDialog"
        >
     <el-result icon="warning" title="注册失败" subTitle="该用户已经注册过，请进行登录">
      <template slot="extra">
         <el-button type="primary" size="medium" @click="openLoginDialog">去登录</el-button>
      </template>
    </el-result>
      </el-dialog>

  </div>
</template>

<script>
export default {
  data(){
      // 注册密码输入验证
       var Pass = (rule, value, callback) => {
        let test = /^[a-zA-Z]\w{5,17}$/
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
            if(test.test(this.registerData.password)){
                callback();    
          }else{
             callback(new Error('必须以字母开头,不能包含中文,且长度在6-18之间'));
          }
        }
      };
            // 登录密码输入验证
       var Pass2 = (rule, value, callback) => {
        let test = /^[a-zA-Z]\w{5,17}$/
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
            if(test.test(this.loginData.password)){
                callback();    
          }else{
             callback(new Error('必须以字母开头,不能包含中文,且长度在6-18之间'));
          }
        }
      };
      // 确认密码验证
      var checkPass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.registerData.password) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      }
      return{
        // 注册表单数据
        registerForm:{
          radio:''
        },
         // 控制找回密码模态框显示隐藏
        dialogRetrievepwd:false,
        // 控制注册模态框显示隐藏
        dialogRegister:false,
        // 控制登录模态框显示隐藏
        dialogLogin:false,
        // 注册成功模态框
        dialogRegisterSuccess:false,
         // 注册失败模态框
        dialogRegisterFail:false,
        // 用户名占用模态框
        dialogOccupy:false,
        // 该用户已注册 模态框
        alreadyDialog:false,
        // 是否记住密码
        rememberPwd:false,
        // 背景图片
        background:require('../assets/images/login/loginbg1.png'),
        // 注册表单数据
        registerData:{
          userName:'',
          password:'',
          repeatPwd:'',
          id:'',
          name:''
        },
        // 登录表单数据
        loginData:{
          userName:'',
          password:'',
          id:''
        },
        // 成功模态框的用户名 密码
        userInfo:{
         userName:'111',
         password:'qqqwwweee'
        },
        // 注册表单校验规则
        registerRules:{
          userName: [
            { required: true, message: '请输入用户名', trigger: 'blur' },
            { min: 4, max: 10, message: '长度在 4 到 10 个字符', trigger: 'blur' }
          ],
           password: [
               { validator: Pass,required: true, trigger: 'blur' },
          ],
           password2: [
               { validator: Pass2,required: true, trigger: 'blur' },
          ],
           repeatPwd: [
               { validator: checkPass,required: true, trigger: 'blur' }
          ],
           id: [
             { required: true, message: '身份码不能为空'},
            { type: 'number', message: '编号必须为数字值'}
          ],
           name: [
            { required: true, message: '请输入姓名', trigger: 'blur' },
            { min: 2, max: 6, message: '长度在 2 到 6 个字符', trigger: 'blur' }
          ],
        }
      }
  },
  methods:{
        // 获取用户信息
  async getUserInfo(){
     let { data:res} = await this.$axios({
      url:`users/getuserInfo?id=${sessionStorage.getItem('id')}`
    })
    let arr =  document.querySelector('.Login').className.split(' ')
     if(!!res.data[0].background){
      switch (res.data[0].background) {
        case 'http://localhost:8080/img/loginbg1.d73501f8.png':
          arr.splice(1,1,'bg1')
          break;
           case 'http://localhost:8080/img/loginbg2.9090118a.png':
          arr.splice(1,1,'bg2')
          break;
           case 'http://localhost:8080/img/loginbg3.24afeab4.png':
          arr.splice(1,1,'bg3')
          break;
           case 'http://localhost:8080/img/loginbg4.6ccb7a1e.png':
          arr.splice(1,1,'bg4')
          break;
           case 'http://localhost:8080/img/loginbg5.3164b0af.png':
          arr.splice(1,1,'bg5')
          break;
        default:
          break;
      }
     }
     let str =  arr.join().replace(',',' ')
    document.querySelector('.Login').className =str
   },
    // 打开登录模态框，并把本地存储内容渲染给模态框
    openDialogLogin(){
    this.dialogLogin = true
   localStorage.getItem('userName')!=null ? this.loginData.userName = localStorage.getItem('userName') :this.loginData.userName=this.loginData.userName
   localStorage.getItem('password')!=null ? this.loginData.password = localStorage.getItem('password') : this.loginData.password =this.loginData.password
   localStorage.getItem('id')!=null ? this.loginData.id = +localStorage.getItem('id') :this.loginData.id = this.loginData.id
  // 将记住密码框的状态 记录到本地存储
  if(localStorage.getItem('rememberPwd')){
    this.rememberPwd = !!localStorage.getItem('rememberPwd') 
  }else{
    this.rememberPwd = false
  }

    },
    //   去往后台页面
       toAdministration(){
      this.$refs.loginForm.validate(async (valid) => {
        // 如果通过表单验证
         if (valid) { 
          let {data:res} = await this.$axios({
          method:'get',
          url:'users/login',
          params:this.loginData
          }) 
            if(res.data.length){
            //  已注册，检查用户名密码是否匹配
             if(res.data[0].username==this.loginData.userName && res.data[0].password==this.loginData.password){
            //  登录成功
            this.$message.success('登录成功')
                // 获取x轴坐标项值    
              let {data:res} = await this.$axios({
               url:'users/readXdata'
               })
             this.$store.commit('getXdata',res.data)
            // 记录用户名
            localStorage.setItem('userName',this.loginData.userName)
            // 记录访问量    
          this.$axios({
          method:'put',
         url:'users/setVisited'
         })
      

            // 记录token
            sessionStorage.setItem('token','login')
            sessionStorage.setItem('id',this.loginData.id)
            if(this.rememberPwd){
              localStorage.setItem('password',this.loginData.password)
               localStorage.setItem('id',this.loginData.id)
                sessionStorage.setItem('id',this.loginData.id)
              localStorage.setItem('rememberPwd',this.rememberPwd)
            }else{
              localStorage.removeItem('password')
               localStorage.removeItem('id')
              localStorage.removeItem('rememberPwd')
            }
            this.$router.replace('/administration')
             }else{
              // 登录失败，用户名或密码不匹配
              this.$message.error('登录失败，请检查您的用户名或密码是否正确，身份码是否有效')
             }
            }else{
              // 没注册
              this.$message.error('登录失败，请检查您的用户名或密码是否正确，身份码是否有效')
            }
          }
          else{
            return false
          }
      }
      )
      },
      // 点击记住密码取消勾选状态 清除本地存储记录
      removeItem(){  
              localStorage.removeItem('password')
               localStorage.removeItem('id')
              localStorage.removeItem('rememberPwd')
      },
      // 点击关闭注册模态框按钮
      closeDialogRegister(){
        this.dialogRegister = false
        this.$refs.registerForm.resetFields()
      },
      // 注册用户
      registerUser(){
        //  校验表单
         this.$refs.registerForm.validate(async (valid) => {
            if (valid) {
          let {data:res} = await this.$axios({
          method:'put',
          url:'users/registerusers',
          data:this.registerData
          }) 
          // 如果用户名已存在
          if(res.status==202){
          this.dialogOccupy=true
          // 如果用户已经进行过注册
          }else if(res.status==204){
           this.alreadyDialog=true
          }else if(res.status==201){
             // 成功注册
             this.userInfo.userName = res.data[0].username
             this.userInfo.password = res.data[0].password
           this.dialogRegister = false
          this.dialogRegisterSuccess= true
          }else{
          //  不存在这个用户
          this.dialogRegisterFail=true
           }   
            }else{
              // 验证不通过
             return false;
            }
         })
      },
      // 注册成功，点击去登录
      openLoginDialog(){
        this.dialogRegisterSuccess = false
        this.alreadyDialog = false
        this.dialogRegister=false
        this.dialogLogin = true
      },
      // 注册失败，返回注册页面
      openRegisterDialog(){
        this.dialogRegisterFail = false
        this.dialogOccupy=false
        this.dialogRegister = true
      }
      
  },
  created(){
    this.getUserInfo()
  }
}
</script>

<style lang="less" scoped>

@keyframes Undershoot {
    100%{
        padding-top: 200px;
    }
}
.Login{
    height: 100%;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
.gologin{
    text-align: center;
    box-sizing: border-box;
    animation: Undershoot 1s forwards;
h2{
    margin: 0;
    font-size: 40px;
}
.el-button{
    margin: 25px;
}
& .el-button:nth-of-type(1){
    background-color: #4682B4;
}
& .el-button:nth-of-type(2){
    background-color: #3CB371;
}
}
.login{
/deep/.el-dialog{
  background-image: url('../assets/images/dialog/dialog1.png');
}
.aboutPwd{
   overflow: hidden;
label{
    float: left;
}
span{
   float: right;
   color: red;
   cursor: pointer;
}
}
}
.register{
  /deep/.el-dialog{
  background-image: url('../assets/images/dialog/dialog2.png');
}
}
}

.bg1{
  background-image: url('../assets/images/login/loginbg1.png');
}
.bg2{
  background-image: url('../assets/images/login/loginbg2.png');
}
.bg3{
  background-image: url('../assets/images/login/loginbg3.png');
}
.bg4{
  background-image: url('../assets/images/login/loginbg4.png');
}
.bg5{
  background-image: url('../assets/images/login/loginbg5.png');
}
</style>