<template>

  <div class="answer">
  
    <div class="choose">
         <el-card shadow="never" class="html">
          <!-- this.$store.state.userInfo -->
       <div v-if="$store.state.userInfo.html=='未完成'">
         <el-avatar></el-avatar>
        <h2>html</h2>
        <el-button type="success" @click="goTest('html')">开始答题</el-button>
       </div>
       <div v-else>
       <el-result icon="success" title="该科目任务已完成">
      <template slot="extra">
        <span>分数：{{$store.state.userInfo.html}}</span>
      </template>
    </el-result>
       </div>
         </el-card>
      <el-card shadow="never" class="css">
        <div v-if="$store.state.userInfo.css=='未完成'">
           <el-avatar></el-avatar>
         <h2>css</h2>
       <el-button type="success" @click="goTest('css')">开始答题</el-button>
        </div>
        <div v-else>
       <el-result icon="success" title="该科目任务已完成">
      <template slot="extra">
        <span>分数：{{$store.state.userInfo.css}}</span>
      </template>
    </el-result>
       </div>
      </el-card>
       <el-card shadow="never" class="js">
         <div v-if="$store.state.userInfo.js=='未完成'">
          <el-avatar></el-avatar>
         <h2>js</h2>
        <el-button type="success" @click="goTest('js')">开始答题</el-button>
         </div>
         <div v-else>
       <el-result icon="success" title="该科目任务已完成">
      <template slot="extra">
       <span>分数：{{$store.state.userInfo.js}}</span>
      </template>
    </el-result>
       </div>
       </el-card>
    </div>
  
  </div>
</template>

<script>
export default {
  data(){
   return{
    // 最近1天的历史记录
   newDate:[],
   }
  },
  methods:{
    // 去往答题页面
  async goTest(val){
          let date = new Date()
          let {data:res} = await this.$axios({
            url:`history/getDate?class=${this.$store.state.userInfo.class}`
          })
        if(res.data.length>0){
          if(res.data[0].date==date.toLocaleDateString()){
          //  今天有任务
            switch (val) {
          case 'html':
          // 什么科目
          sessionStorage.setItem('subject','html')
          // 第几套题
          sessionStorage.setItem('number',this.newDate[0].html)
          break;
          case 'css':
             sessionStorage.setItem('subject','css')
          sessionStorage.setItem('number',this.newDate[0].css)
          break;
          case 'js':
          sessionStorage.setItem('subject','js')
          sessionStorage.setItem('number',this.newDate[0].js)
          break;
        default:
          break;
      }
          this.$router.push('/administration/test')
          }else{
            // 没任务
            this.$message.warning('请先发布任务')
          }
        }else{
          // 没任务
           this.$message.warning('请先发布任务')
        }
        },
        // 获取最近1天的历史记录
       async getHistory(){
          let {data:res} = await this.$axios({
            url:`history/getDate?class=${this.$store.state.userInfo.class}`
          })
        this.newDate = res.data
        },
    },
  created(){
   setTimeout(() => {
     this.getHistory()
   }, 0);
  }

}
</script>

<style lang="less" scoped>
.answer{
      height: 100%;
      margin: 0 auto;
.choose{
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: space-between;
.html,.css,.js{
     width: 30%;
      height: 100%;
  
     text-align: center;
     padding-top: 100px;
     box-sizing: border-box;
.el-avatar{
  width: 150px;
  height: 150px;
  background-image: url('../assets/images/administration/css.png');
  background-repeat: no-repeat;
  background-size: 50%;
  background-position: center;
  border: 1px solid gray;
  box-shadow: 5px 5px 10px gray;
  background-color: #eee;
}
h2{
  font-size: 50px;
}
}
.html .el-avatar{
  background-image: url('../assets/images/administration/html.png');
}
.js .el-avatar{
  background-image: url('../assets/images/administration/JavaScript.png');
}
.el-result{
  font-weight: 700;
  span{
    font-size: 30px;
  }
}
}
}
</style>