<template>
  <div class="score">
    <el-card shadow="never">
    <el-table
    :data="tableData"
    border
    style="width: 100%">
    <el-table-column
      prop="id"
      label="ID"
      width="180">
    </el-table-column>
    <el-table-column
      prop="name"
      label="姓名">
    </el-table-column>
    <el-table-column
      prop="html"
      sortable
      label="html">
    </el-table-column>
    <el-table-column
      prop="css"
      sortable
      label="css">
    </el-table-column>
    <el-table-column
      prop="js"
      sortable
      label="js">
    </el-table-column>
     <el-table-column
      prop="job"
      label="职务">
    </el-table-column>
        <el-table-column
      prop="class"
      label="班级">
    </el-table-column>
  </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
data() {
      return {
        tableData: []
      };
    },
    methods:{
      // 获取学生当日任务分数信息
     async getScoreList(){
      let {data:res} = await this.$axios({
       url:`users/getScore?class=${this.$store.state.userInfo.class}`
      })
      console.log(res.data);
      this.tableData = res.data
      }
    },
    created(){
    setTimeout(() => {
      this.getScoreList()
    }, 0);
    }
}
</script>

