<template>
  <div class="usersInfo">
    <el-card shadow="never">
      <!-- 顶部表单区域 -->
     <div class="userHead">
       <el-form ref="form" :inline="true" class="demo-form-inline" :model="filterUsersData">
    <el-form-item>
    <el-input :clearable="true" placeholder="查询" v-model="filterUsersData.info" @keyup.enter.native="filterUsers"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="filterUsers">查询</el-button>
    <el-button type="primary" @click="dialogAddUsers = true">添加</el-button>
       <el-button type="primary" icon="el-icon-refresh" @click="searchModelChange">{{searchStyle.text}}</el-button>
      <el-tooltip class="item" effect="dark" content="恢复初始表格" placement="right-start">
       <el-button icon="el-icon-refresh-right" circle @click="getUsers()"></el-button>
    </el-tooltip>
  </el-form-item>
       </el-form>
        <div class="goBottom" id="top">
            <el-tooltip class="item" effect="dark" content="去往底部" placement="right-start">
          <a href="#bottom"><el-button type="primary" icon="el-icon-caret-bottom" circle></el-button></a>
            </el-tooltip>
        </div>
     </div>
   <!-- 表格 -->
  <el-table
     id="tableOut"
    :data="pageTableData"
    style="width: 100%"
    :row-class-name="tableRowClassName"
     @selection-change="handleSelectionChange">
    <el-table-column
      label="身份码"
      width="120">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.id }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="姓名"
      width="120">
     <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.name }}</span>
      </template>
    </el-table-column>
      <el-table-column
      label="年龄"
      width="120">
     <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.age }}</span>
      </template>
    </el-table-column>
      <el-table-column
      label="性别"
      width="120">
     <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.sex }}</span>
      </template>
    </el-table-column>
     <el-table-column
      label="班级"
      width="120">
     <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.class }}</span>
      </template>
    </el-table-column>
      <el-table-column
      label="联系方式"
      width="180">
     <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.phone }}</span>
      </template>
    </el-table-column>
      <el-table-column
      label="职位"
      width="120">
     <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.job }}</span>
      </template>
    </el-table-column>
     <el-table-column
      label="注册"
      width="80">
     <template slot-scope="scope">
        <el-tag :type="scope.row.register=='已注册' ? 'success' : 'warning'">{{scope.row.register}}</el-tag>
      </template>
    </el-table-column>
    <el-table-column label="操作" >
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="getFormItemsDate(scope.row)"
         >编辑</el-button>
        <el-button
          size="mini"
          type="danger"
          @click="deleteUsers(scope.row.id)"
          >删除</el-button>
      </template>
    </el-table-column>
    <el-table-column
      type="selection"
      width="80"
      >
   
    </el-table-column>
  </el-table>
  <!-- 分页 -->
 <el-pagination
       background
       @size-change="handleSizeChange"
       @current-change="handleCurrentChange"
      :current-page.sync="nowPage"
      :page-sizes="[3, 5, 7, 10]"
      :page-size="initNum"
      layout="total, sizes, prev, pager, next, jumper"
      :total="tableData.length"
      v-if="!printStyle.ifPrintStyle"
      style="margin-top:25px">
    </el-pagination>
   <!-- 导入 -->
<div class="userBottom">
  <!-- 底部表单 -->
<el-upload
 ref="upload"
  action="http://192.168.43.121:9999/users/import"
   accept=".xlsx,.xls"
  style="text-align:left; margin:15px 0"
  :auto-upload="false">
<el-button slot="trigger" icon="el-icon-folder-opened" style="margin-right:15px">选取文件</el-button>
<el-button icon="el-icon-download" @click="submitUpload">导入</el-button>
<!-- 导出 -->
<el-button icon="el-icon-upload2" @click="exportExcel">导出</el-button>
<el-button icon="el-icon-toilet-paper" @click="printExcel">打印</el-button>
<el-button id="bottom" icon="el-icon-sort" @click="changePrint">{{printStyle.text}}</el-button>
</el-upload>
<div class="goTop">
    <el-tooltip class="item" effect="dark" content="去往顶部" placement="right-start">
  <a href="#top"><el-button type="primary" icon="el-icon-caret-top" circle></el-button></a>
    </el-tooltip>
</div>
</div>


    </el-card>

   <!-- 添加用户模态框 -->
   <div class="addUsersClass">
     <el-dialog
         title="添加用户"
         width="30%"
         :visible.sync="dialogAddUsers"
        >
       <el-form ref="addUsersForm" :rules="addUsersRules" label-width="50px" :model="addUsersData">
        <el-form-item label="姓名" label-width="80px" prop="name">
        <el-input v-model="addUsersData.name"></el-input>
       </el-form-item>
         <el-form-item label="年龄" label-width="80px" prop="age">
        <el-input v-model.number="addUsersData.age"></el-input>
       </el-form-item>
         <el-form-item label="性别" label-width="80px" style="text-align:left" prop="sex">
        <el-radio-group style="margin-left:25px" v-model="addUsersData.sex">
        <el-radio label="男"></el-radio>
        <el-radio label="女"></el-radio>
        </el-radio-group>
      </el-form-item>
        <el-form-item label="联系方式" label-width="80px" prop="phone">
        <el-input v-model="addUsersData.phone"></el-input>
       </el-form-item>
          <el-form-item label="职位" label-width="80px" style="text-align:left" prop="job">
        <el-radio-group style="margin-left:25px" v-model="addUsersData.job">
        <el-radio label="老师"></el-radio>
        <el-radio label="学生"></el-radio>
        </el-radio-group>
      </el-form-item>
        <el-form-item label="班级" label-width="80px" prop="class">
        <el-input v-model="addUsersData.class"></el-input>
       </el-form-item>
       </el-form>
       <span slot="footer" class="dialog-footer">
       <el-button @click="dialogAddUsers = false">取 消</el-button>
       <el-button type="primary"  @click="addusers">确 定</el-button>
      </span>
     </el-dialog>
   </div>

<!-- 修改用户模态框 -->
   <div class="updateUsersClass">
     <el-dialog
         title="修改用户"
         width="30%"
         :visible.sync="dialogUpdateUsers"
         top="5vh"
         @close="$refs.updateUsersForm.clearValidate()"
        >
       <el-form ref="updateUsersForm" :rules="addUsersRules" label-width="50px" :model="UpdateData">
         <el-form-item label="编号" label-width="80px">
        <el-input v-model="UpdateData.id" :disabled="true"></el-input>
       </el-form-item>
        <el-form-item label="姓名" label-width="80px" prop="name">
        <el-input v-model="UpdateData.name"></el-input>
       </el-form-item>
         <el-form-item label="年龄" label-width="80px" prop="age">
        <el-input v-model.number="UpdateData.age"></el-input>
       </el-form-item>
         <el-form-item label="性别" label-width="80px" style="text-align:left" prop="sex">
        <el-radio-group style="margin-left:25px" v-model="UpdateData.sex">
        <el-radio label="男"></el-radio>
        <el-radio label="女"></el-radio>
        </el-radio-group>
      </el-form-item>
        <el-form-item label="联系方式" label-width="80px" prop="phone">
        <el-input v-model="UpdateData.phone"></el-input>
       </el-form-item>
          <el-form-item label="职位" label-width="80px" style="text-align:left" prop="job">
        <el-radio-group style="margin-left:25px" v-model="UpdateData.job">
        <el-radio label="老师"></el-radio>
        <el-radio label="学生"></el-radio>
        </el-radio-group>
      </el-form-item>
        <el-form-item label="班级" label-width="80px" prop="class">
        <el-input v-model="UpdateData.class"></el-input>
       </el-form-item>
       </el-form>
       <span slot="footer" class="dialog-footer">
       <el-button @click="dialogUpdateUsers = false">取 消</el-button>
       <el-button type="primary"  @click="updateUsers">确 定</el-button>
      </span>
     </el-dialog>
   </div>


  </div>
</template>

<script>
import FileSaver from "file-saver";
import XLSX from "xlsx";
export default {
 data() {
  //  自定义校验规则
    // 电话号码
     var phoneRules = (rule, value, callback) => {
      let myRule = /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/
       if(myRule.test(value)){
        callback()
       }else{
        callback('您输入的电话号码不合法')
       }
      };
      return {
        // 用于保存用户列表数据
        tableData:[],
        // 用于分页渲染的数据
        pageTableData:[],
        // 控制添加用户模态框的显示与隐藏
        dialogAddUsers:false,
         // 控制修改用户模态框的显示与隐藏
        dialogUpdateUsers:false,
        // 初始每页显示多少数据
        initNum:5,
        // 当前页数
        nowPage:1,
        // 查询模式
        searchStyle:{
          name:'vague',
          text:'模糊查询'
        },
        // 复选框选择的数据
         selectionData:[],
        // 打印模式
        printStyle:{
          ifPrintStyle:false,
          text:'分页模式'
        },
        // 保存修改的数据
        UpdateData:{},
        // 过滤列表查询关键字
        filterUsersData:{
          info:'',
        },
        // 添加用户数据
        addUsersData:{
          name:'',
          age:'',
          sex:'',
          phone:'',
           job:'',
          class:'',   
        },
        // 添加用户表单校验规则
          addUsersRules: {
          name: [
            { required: true, message: '请输入姓名', trigger: 'blur' },
            { min: 2, max: 6, message: '长度在 2 到 6 个字符', trigger: 'blur' }
          ],
           age: [
            { required: true, message: '年龄不能为空'},
             { type: 'number', message: '年龄必须为数字值'}
          ],
            sex: [
            { required: true, message: '请选择您的性别', trigger: 'blur' }
          ],
           phone: [
            { required: true,validator: phoneRules, trigger: 'blur' }
             ],
            job: [
            { required: true, message: '请选择您的身份', trigger: 'blur' }
          ],
           class: [
            { required: true, message: '请填写您所在的班级名称', trigger: 'blur' }
          ],
          }
      }
    },
     methods:{
  // 获取用户列表
 async getUsers(){
    let { data:res} = await this.$axios({
      url:'users/getusers'
    })
  this.pageTableData=this.tableData =res.data
  if(this.printStyle.ifPrintStyle){
    return
  }else{
    this.handleSizeChange(this.initNum)
    this.handleCurrentChange(this.nowPage)
  }
   res.status===200 ? this.$message.success('已更新列表') : this.$message.error('操作失败');
  },
  // 切换打印模式，或分页模式
  changePrint(){
  if(this.printStyle.ifPrintStyle){
     this.printStyle.ifPrintStyle=false
      this.printStyle.text='分页模式'
      this.filterUsers()
  }else{
    this.printStyle.ifPrintStyle=true
      this.printStyle.text='打印模式'
       this.pageTableData=this.tableData
  }
  },
  //  表格隔行变色
      tableRowClassName({row, rowIndex}) {
        if (rowIndex === 1) {
          return 'warning-row';
        } else if (rowIndex === 3) {
          return 'success-row';
        }
        return '';
      },
      // 添加用户
     async addusers(){
        //  校验表单
        this.$refs.addUsersForm.validate(async (valid) => {
          if (valid) {
             let { data:res} = await this.$axios({
          method:'post',
          url:'users/addusers',
          data:this.addUsersData
          }) 
            // 重置表单
            this.$refs.addUsersForm.resetFields() 
            // 关闭模态框
            this.dialogAddUsers=false
            // 查询拉取数据
            this.getUsers()
            // 用户提示消息
          res.status===201 ? this.$message.success('添加成功') : this.$message.error('添加失败');
          } else {
            return false;
          }
        }); 
      },
      // 筛选用户列表
     async filterUsers(){
       let { data:res} = await this.$axios({
          url:`users/filterusers?model=${this.searchStyle.name}`,
          params:this.filterUsersData
          }) 
       this.tableData =res.data
       if(this.printStyle.ifPrintStyle){
        this.pageTableData=this.tableData
        return
       }else{
       this.handleSizeChange(this.initNum)
        }
 },
     //  切换查询模式
    searchModelChange(e){
     if(this.searchStyle.name==='vague'){
        this.searchStyle.name = 'accurate'
         this.searchStyle.text='精准查询'
     }else{
       this.searchStyle.name = 'vague'
         this.searchStyle.text='模糊查询'
     }
    },
    // 得到表格列表用户信息
    getFormItemsDate(scope){
      // 深拷贝对象
     let obj =  structuredClone(scope)
      // 将该用户信息保存到data属性中
      this.UpdateData = obj
    //  打开修改用户模态框
    this.dialogUpdateUsers=true
    },
    // 修改用户
    updateUsers(){
     //  校验表单
        this.$refs.updateUsersForm.validate(async (valid) => {
          if (valid) {
          let { data:res} = await this.$axios({
          method:'put',
          url:'users/updateusers',
          data:this.UpdateData
          }) 
            // 重置表单
            this.$refs.updateUsersForm.resetFields() 
            // 关闭模态框
            this.dialogUpdateUsers=false
            // 查询拉取数据
            this.getUsers()
            // 用户提示消息
          res.status===201 ? this.$message.success('修改成功') : this.$message.error('修改失败');
          } else {
            return false;
          }
        }); 
    },
    // 删除用户
    deleteUsers(val){
      this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async () => {
          // 多选删除
          if(this.selectionData.length){
            let arr = []
         this.selectionData.forEach((item) => {
             arr.push(item.id)
         })
          let {data:res} = await this.$axios({
          method:'delete',
          url:'users/deleteusers',
          data: {id:arr}
          })
            setTimeout(() => {
               this.getUsers()
            },500)
             res.status==202 ? this.$message.warning('用户已注册，不允许删除') : this.$message.success('删除成功!');
          }
          // 单选删除
          else{
            let {data:res} = await this.$axios({
          method:'delete',
          url:'users/deleteusers',
          data: {id:val}
          })
            this.getUsers()
           res.status==202 ? this.$message.warning('用户已注册，不允许删除') : this.$message.success('删除成功!');
          }  
       }).catch(() => {
          this.$message.info('已取消删除');          
        });
      
    },
    // 分页器每页显示条数改变时触发
    handleSizeChange(count){
      // 将原始数据过滤，渲染到表格
    this.pageTableData=this.tableData.filter((item,index) => index<count)
    // 重新赋值初始页码值，保证重新拉取数据的时候，分页器不会被初始化
     this.initNum=count
    },
    // 分页器页码变化触发
    handleCurrentChange(val){
         this.pageTableData=this.tableData.filter((item,index) => index>=val* this.initNum-this.initNum && index<val* this.initNum)
    },
    // 上传Excel文件
     submitUpload() {
        this.$refs.upload.submit();
        setTimeout(() => {
          this.getUsers()
        }, 500);
      },
      // 导出Excel
       exportExcel () {
      var xlsxParam = { raw: true }
      var wb = XLSX.utils.table_to_book(
        document.querySelector('#tableOut'),
        xlsxParam
      )
      var wbout = XLSX.write(wb, {
        bookType: 'xlsx',
        bookSST: true,
        type: 'array'
      })
      try {
        FileSaver.saveAs(
          new Blob([wbout], { type: 'application/octet-stream' }),
          '用户个人信息.xlsx'
        )
      } catch (e) {
        if (typeof console !== 'undefined') this.$message.warning('导出失败')
      }
      return wbout
    },
    //  复选框被勾选触发
      handleSelectionChange(val) {
     this.selectionData = val
      },
      // 打印
printExcel(){
//调用打印功能
window.print();

}
     },
 created(){
  this.getUsers()
},
}
</script>

<style lang="less" scoped>
.usersInfo{
    line-height: 30px;
.userHead{
  display: flex;
  justify-content: space-between;
.goBottom{
display: flex;
align-items: center;
margin-right: 100px;
}
}
.userBottom{
   display: flex;
  justify-content: space-between;
.goTop{
display: flex;
align-items: center;
margin-right: 100px;
height: 70px;
}
}
.el-form{
  text-align: left;
}
.el-table{
/deep/th{
  text-align: center;
}
/deep/.el-table__cell{
  text-align: center !important;
}
/deep/.warning-row {
    background: oldlace;
}

/deep/.success-row {
    background: #f0f9eb;
}
}
.addUsersClass{
    /deep/.el-dialog{
  background-image: url('../assets/images/dialog/dialog3.png');
}
}
.updateUsersClass{
   /deep/.el-dialog{
  background-image: url('../assets/images/dialog/dialog4.png');
}
}
}
</style>